/*Обнуление*/
*{padding: 0;margin: 0;border: 0;}
*,*:before,*:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:16px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: inherit;}
/*--------------------*/

.wrapper{
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: "'Montserrat', sans-serif'";
}

.body{
    margin-top: 57px;
    flex: 1 1 auto;
    max-height: calc(100vh - 3px);
}

.input1{
    display: flex;
    width: 90%;
    height: 59%;
    box-shadow: 0 0 1px black;
    margin-top: 10px;
}



.inputContainer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: white;
    border-top: #005475 solid 3px;
}

.inputColumn{
    display: flex;
    flex-direction: column;
    /*border: black solid 1px;*/

}

.inputRow1{
    display: flex;
    margin-top: 57px;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    padding: 0 1rem;


    div{
        display: flex;
        flex: 1 0 auto;
        align-items: center;
    }

    input{
        padding: 0 1em;
        height: 1.5rem;
        width: 12rem;
        font-size: 16px;
        text-transform: uppercase;
        border-bottom: 1px solid #005475;
    }
    @media screen and (max-width: 375px) {
        input {
            width: 7rem;
        }
    }
    @media screen and (max-width: 440px) {
        input {
            width: 10rem;
        }
    }

    img{
        display: inline-block;
        border-bottom: #005475 1px solid;
        height: 1.5rem;

    }

    select{
        display: inline-block;
        font-family: "'Montserrat', sans-serif";
        font-size: 16px;
        height: 1.5rem;
        border-bottom: #005475 1px solid;
    }
}

.first{
    justify-content: center;
    padding: 0;
    width: 100%;

    select{
        font-size: 16px;
        font-family: "'Montserrat', sans-serif";
        width: 130px;
        text-align: center;
    }
}

.second{
    justify-content: center;
}

.inputRow2{
    display: flex;
    padding: 1rem 2.5rem;
    /*border: #005475 1px solid;*/
    width: 100%;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 395px) {
        padding: 1.5rem 2rem;
    }
    @media screen and (max-width: 370px) {
        padding: 1.5rem 1.5rem;
    }
    @media screen and (max-width: 350px) {
        padding: 1.5rem 0.5rem;
    }


    img{
        display: inline-block;
        width: 30px;
        height: 30px;
        box-shadow: 0 0 3px black;
        border-radius: 10px;

    }

    button{
        height: 2rem;
        width: 11rem;
        background: #005475;
        color: white;
        font-size: 16px;
        border: #005475 1px solid;
        border-radius: 5px;

    }

    div{
        display: flex;
        flex: 1 1 auto;
        /*border: 1px solid #005475;*/
        justify-content: center;
    }
}

.image{
    margin: 0 1rem;

}