.wrapper{
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  font-family: "'Montserrat', sans-serif'";
}

.column{
  background-color: #fefefe;
  margin: 40% auto;
  padding: 20px;
  width: 80%;
  max-width: 400px;
  max-height: 80%;
  display: flex;
  flex-direction: column;
  border: 1px solid #888;
  overflow: auto;
  border-radius: 5px;
}

.title{
    font-size: 2rem;
    width: 100%;
    padding: 5px;
    border-bottom: #005475 2px solid;
}

.list{
    margin-top: 20px;
}

.selectList {
    /*position: absolute;*/
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    /*border: 1px solid #ccc;*/
    max-height: 200px;
    overflow-y: auto;
  }
  
  .selectList li {
    padding: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
      font-size: 1.2rem;
  }
  
  .selectList li:hover {
    background-color: #f0f0f0;
  }
  
  .selectList input {
    margin-right: 10px;
  }

  .btn{
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      padding: 2rem 0 0 0;

      button{
          height: 2rem;
          width: 10rem;
          background: #005475;
          color: white;
          font-size: 16px;
          border: #005475 1px solid;
          border-radius: 5px;

      }
  }

.close{
    position: absolute;
    float: right;
    top: 138px;
    right: 16px;
}